// src/components/renderer/layouts/LayoutHero.tsx
import { SlideData, Language } from '@/core/types';
import { PhoneMockup } from '../PhoneMockup';
import { Star } from 'lucide-react';

export const LayoutHero = ({ data, lang }: { data: SlideData; lang: Language }) => {
  const content = data.content[lang];

  // Renk haritası: Arka plan, metin ve vurgu renkleri
  const colorMap: Record<string, { bg: string, text: string, accent: string, blob: string }> = {
    blue: { bg: 'bg-blue-50', text: 'text-blue-900', accent: 'bg-blue-500', blob: 'bg-blue-300' },
    indigo: { bg: 'bg-indigo-50', text: 'text-indigo-900', accent: 'bg-indigo-500', blob: 'bg-purple-300' },
    purple: { bg: 'bg-purple-50', text: 'text-purple-900', accent: 'bg-purple-500', blob: 'bg-pink-300' },
    rose: { bg: 'bg-rose-50', text: 'text-rose-900', accent: 'bg-rose-500', blob: 'bg-orange-300' },
    emerald: { bg: 'bg-emerald-50', text: 'text-emerald-900', accent: 'bg-emerald-500', blob: 'bg-teal-300' },
    yellow: { bg: 'bg-yellow-50', text: 'text-yellow-900', accent: 'bg-yellow-500', blob: 'bg-orange-300' },
    cyan: { bg: 'bg-cyan-50', text: 'text-cyan-900', accent: 'bg-cyan-500', blob: 'bg-blue-200' },
  };

  const theme = colorMap[data.themeColor] || colorMap.blue;

  // Store Butonları (SVG)
  const StoreButtons = () => (
    <div className="flex gap-3 justify-center mt-6">
        {/* App Store */}
        <div className="bg-slate-900 text-white px-4 py-2 rounded-lg flex items-center gap-2 shadow-lg cursor-pointer hover:scale-105 transition-transform">
            <svg viewBox="0 0 384 512" fill="currentColor" className="w-6 h-6"><path d="M318.7 268.7c-.2-36.7 16.4-64.4 50-84.8-18.8-26.9-47.2-41.7-84.7-44.6-35.5-2.8-74.3 20.7-88.5 20.7-15 0-49.4-19.7-76.4-19.7C63.3 141.2 4 184.8 4 273.5q0 39.3 14.4 81.2c12.8 36.7 59 126.7 107.2 125.2 25.2-.6 43-17.9 75.8-17.9 31.8 0 48.3 17.9 76.4 17.9 48.6-.7 90.4-82.5 102.6-119.3-65.2-30.7-61.7-90-61.7-91.9zm-56.6-164.2c27.3-32.4 24.8-61.9 24-72.5-24.1 1.4-52 16.4-67.9 34.9-17.5 19.8-27.8 44.3-25.6 71.9 26.1 2 52.3-11.4 69.5-34.3z"/></svg>
            <div className="flex flex-col items-start leading-none">
                <span className="text-[8px] uppercase opacity-80">Download on the</span>
                <span className="text-sm font-bold">App Store</span>
            </div>
        </div>
        {/* Google Play */}
        <div className="bg-slate-900 text-white px-4 py-2 rounded-lg flex items-center gap-2 shadow-lg cursor-pointer hover:scale-105 transition-transform">
            <svg viewBox="0 0 512 512" fill="currentColor" className="w-6 h-6"><path d="M325.3 234.3L104.6 13l280.8 161.2-60.1 60.1zM47 0C34 6.8 25.3 19.2 25.3 35.3v441.3c0 16.1 8.7 28.5 21.7 35.3l256.6-256L47 0zm425.2 225.6l-58.9-34.1-65.7 64.5 65.7 64.5 60.1-34.1c18-14.3 18-46.5-1.2-60.8zM104.6 499l280.8-161.2-60.1-60.1L104.6 499z"/></svg>
            <div className="flex flex-col items-start leading-none">
                <span className="text-[8px] uppercase opacity-80">Get it on</span>
                <span className="text-sm font-bold">Google Play</span>
            </div>
        </div>
    </div>
  );

  return (
    <div className={`w-full h-full relative overflow-hidden flex flex-col items-center pt-24 ${theme.bg}`}>
      
      {/* Dekoratif Arkaplan Şekilleri (Blobs) */}
      <div className={`absolute -top-20 -left-20 w-96 h-96 rounded-full mix-blend-multiply filter blur-3xl opacity-40 ${theme.blob}`} />
      <div className={`absolute top-40 -right-20 w-80 h-80 rounded-full mix-blend-multiply filter blur-3xl opacity-40 ${data.themeColor === 'yellow' ? 'bg-yellow-300' : 'bg-indigo-200'}`} />

      {/* Metin Alanı - Daha Zengin */}
      <div className="z-20 text-center flex flex-col items-center space-y-4 max-w-4xl px-8 relative">
        
        {/* Üst Rozet */}
        <div className="inline-block">
            <span className={`px-4 py-1 rounded-full text-sm font-bold shadow-sm uppercase tracking-wider bg-white ${theme.text} font-[var(--font-fredoka)] flex items-center gap-2`}>
                <Star size={14} fill="currentColor" />
                {content.subtitle}
            </span>
        </div>

        <h1 className={`text-7xl font-[var(--font-luckiest)] ${theme.text} leading-tight drop-shadow-sm`}>
          {content.title}
        </h1>

        <p className={`text-xl font-medium ${theme.text} opacity-80 max-w-2xl font-[var(--font-fredoka)]`}>
            {(content as any).description}
        </p>

        {/* Store Butonları */}
        <StoreButtons />

        <div className={`mt-4 text-sm font-bold ${theme.text} opacity-60 flex items-center gap-1`}>
            ⭐⭐⭐⭐⭐ {(content as any).rating}
        </div>
      </div>

      {/* Telefon (3D) */}
      <div className="absolute inset-0 top-32 flex items-center justify-center perspective-1000 pointer-events-none">
         {/* perspective-1000 sınıfı 3D efekti için şart */}
         <div className="relative w-full h-full flex items-center justify-center" style={{ perspective: '1000px' }}>
            <PhoneMockup phone={data.phone} image={data.image} />
         </div>
      </div>
    </div>
  );
};